.learn_button::before {
    content: "";
    position: absolute;
    inset: 0;
    background-image: linear-gradient(to left, #00d581, #00d581);
    z-index: 1;
    transition: opacity 0.3s ease-in;
    opacity: 0;
    border-radius: 5px;
}

.learn_button:hover::before {
    opacity: 1;
    z-index: -1;
}

.learn_button_link {
    text-decoration: none;
    color: #fff;
}

.home_img {
    width: 30%;
    height: 30%;
    top: 0;
    left: 0;
    margin-left: -10rem;
    margin-top: -5rem;
    animation: home_img_slide 6s ease-in-out infinite;
}

@keyframes home_img_slide {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(1rem);
    }

    100% {
        transform: translateY(0);
    }
}

