.platform_card {
    width: 19.5rem;
    height: 18.125rem;
    border: none;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    z-index: 2;
    padding-top: 2.625rem;
    padding-right: 1.875rem;
    padding-bottom: 1.875rem;
    padding-left: 1.875rem;
    cursor: pointer;
}

.platform_card_selected {
    width: 19.5rem;
    height: 18.125rem;
    border: none;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
